/** =============FOOTER============= */
.footer{
    padding: 40px 0;
    background-color: #242438;
}
.footer__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer__text{
    color: #fff;
    font-weight: 600;
    letter-spacing: 3px;
}
.footer__arrow a{
    color: #fff;
    font-size: 30px;
}

/** =============FOOTER MOBILE============= */
@media(max-width:992px){
    .footer .container{
        max-width: 95%;
    }
    .footer__text{
        font-size: 12px;
    }
}