/** =============CONTACT============= */
.contact{
    padding: 80px 0;
}
.contact__form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 20px;
}
.contact__input{
    border: 2px solid #fff;
    padding: 10px;
    width: 500px;
    border-radius: 5px;
    color: #fff;
}
.contact__message{
    width: 500px;
    height: 200px;
    border: 2px solid #fff;
    padding: 10px;
    resize: none;
    outline: none;
    color: #fff;
    border-radius: 5px;

    margin-bottom: 30px;
}
.contact__btn{
    padding: 15px 50px;
    background-color: var(--main-color);
    border-radius: 20px;
    color: #fff;
    font-size: 20px;
}

/** =============CONTACT MOBILE============= */
@media(max-width:550px) {
    .contact__input{
        max-width: 350px;
    }
    .contact__message{
        max-width: 350px;

        margin-bottom: 10px;
    }
}