/** =============SERVICES============= */
.services{
    padding: 80px 0;
}
.services__container{
    padding: 0 100px;
}
.services__box{
    display: flex;
    justify-content: center;
    column-gap: 32px;
    row-gap: 42px;
    flex-wrap: wrap;
}
.services__card{
    display: flex;
    flex-direction: column;
    padding: 30px;
    max-width: 350px;
    background-color: rgba(53, 190, 190, 0.761);
    border-radius: 10px 20px 10px 20px;
}
.services__card-icon{
    text-align: center;
    font-size: 50px;
    color: var(--title-color);

    margin-bottom: 12px;
}
.services__card-title{
    text-align: center;
    font-size: 30px;
    letter-spacing: 2px;
    font-weight: 600;
    color: var(--title-color);

    margin-bottom: 12px;
}
.services__card-text{
    text-align: center;
    color: var(--title-color);
    font-weight: 400;

    margin-bottom: 30px;
}
.services__card-btn{
    text-align: center;
    margin: 0  auto;
    border: 2px solid #fff;
    padding: 10px 40px;
    width: max-content;

    color: var(--title-color);
    text-transform: uppercase;
}
.services__card-btn:hover{
    background-color: #fff;
    color: #000;
    transition: .2s all ease;
}

/** =============SERVICES MOBILE============= */
@media(max-width:560px) {
    .services__container{
        padding: 0 15px;
    }
}
@media(max-width:400px) {
    .services__card{
        max-width: 300px;
    }
    .services__card-title{
        font-size: 25px;
    }
}