/** =============ABOUT============= */
.about{
    padding: 50px 0;
}
.about__container{
    display: flex;
    justify-content: space-between;
    gap: 30px;
}
.about__img{
    border-radius: 10px;
    max-width: 400px;
}
.about__content{
    display: flex;
    flex-direction: column;
}
.about__text{
    max-width: 500px;
    color: var(--text-color);

    margin-bottom: 40px;
}
.about__btn{
    width: max-content;
    background-color: var(--main-color);
    padding: 15px 30px;
    border-radius: 10px;

    color: var(--title-color);
    font-weight: 400;
}
.about__box{
    display: flex;
    column-gap: 30px;
}
.about__card{
    display: flex;
    flex-direction: column;
    background-color: rgba(53, 190, 190, 0.761);
    padding: 30px;
    max-width: 200px;
    border-radius: 15px;

    margin-bottom: 30px;
}
.about__card-icon{
    text-align: center;
    font-size: 30px;
    margin-bottom: 12px;
    color: var(--title-color);
}
.about__card-title{
    text-align: center;
    color: var(--title-color);
    font-weight: 600;
    font-size: 20px;

    margin-bottom: 20px;
}
.about__card-subtitle{
    text-align: center;
    color: var(--title-color);
}

/** =============ABOUT MOBILE============= */
@media(max-width:1080px) {
    .about .container{
        max-width: 90%;
    }
    .title{
        margin-bottom: 50px;
    }
}
@media(max-width:900px)  {
    .about__container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about__content{
        text-align: center;
    }
    .about__box{
        margin: 0 auto;
    }
    .about__btn{
        margin: 0 auto;
    }
}
@media(max-width:500px) {
    .title{
        margin-bottom: 20px;
    }
    .about__img{
        width: 250px;
    }
    .about__box{
        flex-direction: column;
    }
    .about__card{
        max-width: 400px;
        margin-bottom: 30px;
    }
    .about__text{
        margin-bottom: 30px;
    }
    .subtitle{
        font-size: 20px;
    }
    .title{
        font-size: 35px;
    }
}