@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700;800&display=swap');

/* Container */
.container{
  max-width: 75%;
  margin: 0 auto;
}

/* Reset and base styles  */
:root{
  --bg-color: #10101a;
  --main-color: #0aa6a4;
  --title-color: #fff;
  --text-color: #9a9a9a;
  --nav-color: #171c36;
}
body{
  background-color: var(--bg-color);
}
.subtitle{
  text-align: center;
  color: var(--text-color);
  font-size: 25px;
  font-weight: 400;
}
.title{
  text-align: center;
  font-size: 40px;
  color: var(--main-color);
  letter-spacing: 2px;

  margin-bottom: 80px;
}

* {
	padding: 0px;
	margin: 0px;
	border: none;
  font-family: 'Outfit', sans-serif;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

:focus,
:active {
	/*outline: none;*/
}

a:focus,
a:active {
	/* outline: none;*/
}

/* Links */

a, a:link, a:visited  {
    /* color: inherit; */
    text-decoration: none;
    /* display: inline-block; */
}

a:hover  {
    /* color: inherit; */
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}