/** =============HEADER============= */
.header{
    padding-top: 20px;
    padding-bottom: 100px;
}
.header__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    margin-top: 60px;
}
.header__img{
    width: 120px;
    height: 120px;
    border-radius: 50%;

    margin-bottom: 20px;
}
.header__subtitle{
    color: var(--text-color);
    font-size: 20px;
    font-weight: 400;

    margin-bottom: 10px;
}
.header__title{
    max-width: 550px;
    text-align: center;

    color: var(--title-color);
    font-size: 56px;
    font-weight: 400;

    margin-bottom: 30px;
}
.header__btn{
    background-color: var(--main-color);
    padding: 20px 48px;
    font-size: 20px;
    color: var(--title-color);
    border-radius: 4px;
    border: 1px solid #ffffff61;
}
.header__btn:hover{
    background-color: rgb(46, 159, 159);
    transition: .2s all ease;
}

/** =============HEADER MOBILE============= */
@media(max-width:600px) {
    .header__title{
        font-size: 40px;
    }
    .header__btn{
        font-size: 20px;
        padding: 20px 40px;
    }
}
@media(max-width:400px) {
    .header__title{
        font-size: 35px;
    }
    .header__btn{
        padding: 12px 30px;
        font-size: 16px;
    }
}
@media(max-width:320px) {
    .header__title{
        font-size: 30px;
        line-height: 35px;
    }
}