/** =============Navbar============= */
.navbar{
    background-color: #171c3697;
    width: max-content;
    padding: 10px 30px;
    position: fixed;
    z-index: 1000;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
    display: flex;
    column-gap: 20px;
    border-radius: 20px;
    backdrop-filter: blur(15px);
}
.navbar a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--title-color);
    font-size: 22px;
}
.navbar a:hover{
    background: rgba(0,0,0,0.2);
}
.navbar a.active{
    background: #30304e;
}